.title {
    grid-area: title;
    display: grid;
    grid-gap: 20px;
    width: 100%;
    max-width: 700px;
    grid-template-columns: 1fr auto 1fr;
    align-items:center;
}
.title:before,
.title:after {
    display:black;
    content:'';
    height:10px;

}

.title:before {
    background: linear-gradient(to left, var(--gold),transparent);
}
.title:after {
    background: linear-gradient(to right, var(--gold),transparent);
}